










export default {
	data() {
		return {
			bgSrc: '../../static/logo.png',
			imgSrc: '../../static/home.png',
			
			pic: '',
		};
	},
	onLoad() {
		this.save();
	},
	methods: {
		saveImageToPhotosAlbum(){
			const vm = this;
			uni.saveImageToPhotosAlbum({
			  filePath: vm.pic,
			  success(res) {
				uni.showToast({
				  title: '保存成功',
				  icon: 'success',
				  duration: 2000
				});
			  }
			});
		},
		saveImage() {
		  let self = this;
		  const ctx = uni.createCanvasContext('myCanvas');
		  ctx.drawImage(self.bgSrc, 0, 0, 375, 417);
		  ctx.drawImage(self.imgSrc, 57, 44, 260, 264);
		  ctx.draw(false, function(e) {
			// 保存到本地
			uni.canvasToTempFilePath({
			  x: 0,
			  y: 0,
			  width: 375,
			  height: 417,
			  canvasId: 'myCanvas',
			  success: function(res) {
				let pic = res.tempFilePath;
				self.pic = pic;
				
			  }
			});
		  });
		},
		save() {
		  let self = this;
		  if (!this.imgSrc) {
			uni.showToast({
			  title: '请先选择图片',
			  icon: 'none',
			  duration: 2000
			});
			return false;
		  };
		  
			uni.getSetting({
				success(res) {
					if (!res.authSetting['scope.writePhotosAlbum']) {
					uni.authorize({
						scope: 'scope.writePhotosAlbum',
						success() {
						self.saveImage();
						}
					});
					} else {
					self.saveImage();
					}
				}
			});
		},
	},
}
